<template>
	<div class="tableBody">
		<table class="tableBody-table" border="1" cellspacing="0" cellpadding="0">
			<thead>
				<tr class="tableBody-tr">
					<th colspan="2" >项目及考核内容</th>
					<th colspan="2"> 分值	</th>
					<th>得分合计</th>
					<th>操作</th>
				</tr>
			</thead>
			<tbody  >
				<tr v-for="item in tableData" :key="item.id" class="tableBody-tr tableBody-tr-two"  >
					<td v-show="item.isHidden" :rowspan="item.rowspan"  class="vertical" >
						{{item.type}}
					</td>
					<td>{{item.content}} </td>
					<td>{{item.cate}}</td>
					<td>{{item.score}}</td>
					<td>{{item.score}}</td>
					<td class='td-text-center' @click="showMore(1)">详情</td>
				</tr>
			</tbody>
		</table>

		<div class="tip-box" v-show='tipFlag'>
			<span class="close"  @click.stop="showHide()"></span>
			<ul class="tip-list" >
				<li class="tip-list-text">
					<span>项目：</span><span>{{tipList.type}}</span>
				</li>
				<li class="tip-list-text"><span>考核内容：</span><span>{{tipList.content}}</span></li>
				<li class="tip-list-text"><span>分值：</span><span>{{tipList.cate}}</span></li>
				<li class="tip-list-text"><span>分值：</span><span>{{tipList.score}}</span></li>
				<li class="tip-list-text"><span>直属部门领导或部门总监：</span><span>{{tipList.zsld}}</span></li>
				<li class="tip-list-text"><span>其他部门领导：</span><span>{{tipList.qtld}}</span></li>
				<li class="tip-list-text"><span>部门总监或其他部门总监：</span><span>{{tipList.bmzj}}</span></li>
				<li class="tip-list-text"><span>得分合计：</span></li>
			</ul>
		</div>
	</div>
</template>

<style>
.tableBody{
	width: 100%;
	height: 88%;
	padding:6px 12px;
	font-size: 16px;
}
.tableBody-table{
	width: 100%;
	padding:6px 12px;
	font-size: 16px;
}

.tableBody-tr{
	width: 100%;
	border:1px solid #000;
}

.vertical{
	vertical:center;
}
tr > th{
	padding:4px 0;
}
tr > td{
	padding:1px 0;
	max-width:60px;
	word-wrap: break-word;
	font-size: 14px;
}
.td-text-center{
	text-align: center;
}
/*  弹出框 */
.tip-box{
	width:100%;
	height:100%;
	background-color: #e0e0dc;
	position: absolute;
	top:0;
	left:0;
}
.tip-list{
	width: 90%;
	height:80%;
	position:absolute;
	top:10%;
	left:5%;
	background-color: #fbfbf9;
	padding:12px;
	font-size: 26px;
}
.tip-list-text{
	width:100%;
	line-height: 40px;
	letter-spacing: 2px;
}

/* 关闭按钮 */
.close{
	position:absolute;
	top:10%;
	right:10%;
	width:0.4rem;
	height:0.4rem;
	z-index: 99;
	background-color: none;
		transform: rotate(45deg);

}
.close::before{
	content: "";
	position:absolute;
	left:50%;
	width:0.05rem;
	height:0.4rem;
	background: #333;
	transform: rotate(0deg) translateX(-50%);
	display: inline-block;
	
}
.close::after{
	content: "";
	position: absolute;
	top: 0;
	left:50%;
	width:0.05rem;
	height:0.4rem;
	background: #333;
	transform: translateX(-50%) rotate(90deg);
}
</style>

<script>
	export default {
		name: 'tableBody',
		data(){
			return{
				msg:'this is tableBody.vue test',
				tableData:[
				{
					"id":"1","type":"出勤考核5%","content":"全勤","cate":"优秀","score":"5","zsld":"张三","qtld":"李四","bmzj":"杨广",
					"rowspan":"","isHidden":false
				},
				{
					"id":"2","type":"出勤考核5%","content":"全勤","cate":"优秀","score":"5","zsld":"张三","qtld":"李四","bmzj":"杨广","rowspan":"","isHidden":false
				},
				{
					"id":"3","type":"日常管理5%","content":"全勤","cate":"优秀","score":"5","zsld":"张三","qtld":"李四","bmzj":"杨广",
					"rowspan":"","isHidden":false
				},
				{
					"id":"4","type":"出勤考核5%","content":"全勤","cate":"优秀","score":"5","zsld":"张三","qtld":"李四","bmzj":"杨广","rowspan":"","isHidden":false
				},
				{
					"id":"5","type":"日常管理5%","content":"全勤","cate":"优秀","score":"5","zsld":"张三","qtld":"李四","bmzj":"杨广","rowspan":"","isHidden":false
				}
				],
				newTableData:[],
				tipList:{
					"id":"1","type":"出勤考核5%","content":"全勤","cate":"优秀","score":"5","zsld":"张三","qtld":"李四","bmzj":"杨广",
					"rowspan":"","isHidden":false
				},
				tipFlag:false

			}
		},
		methods:{
			////数组对象排序 原理利用sort()
			listSort(prop){
				return function (obj1, obj2) {
					var val1 = obj1[prop];
					var val2 = obj2[prop];if (val1 < val2) {
						return -1;
					} else if (val1 > val2) {
						return 1;
					} else {
						return 0;
					}
				}
			},
			//// getRepeatNum 获取数组中重复值的个数
			getRepeatNum(titleList){
				let obj = {};
				let arr = [];
				let pushArr= []
				for(let i= 0, l = titleList.length; i< l; i++){
					let item = titleList[i].type;
					obj[item] = (obj[item] +1 ) || 1;
				}

				for(let j in obj){
					var indexOf4 = (titleList || []).findIndex((item) => item.type == j);
					arr.push(indexOf4);
				}

				console.log(obj,arr);
				return [obj,arr];
			},
			//// 更改数组对象中的元素
			getResetList(list){
				let objList = this.$options.methods.getRepeatNum(this.tableData.sort(this.$options.methods.listSort("type")));
				for (var i = 0;i < list.length;i++) {
					for(let j in objList[0]){
						if(list[i].type == j){
							list[i].rowspan = objList[0][j];
						}
					};
					for (let j in objList[1]) {
						if(i == objList[1][j]){
							list[i].isHidden = true;
						}
					};
				}
				return list;
			},

			////showMore 详情点击事件
			showMore(){
				this.tipFlag = true;
			},
			showHide(){
				this.tipFlag = false;
			}

		},

		created:function(){

			this.newTableData = this.getResetList(this.tableData);


		},
		beforeMount:function(){
			console.log(this.newTableData);


		},
		mounted:function(){


		}


	}
</script>



